import "./app.css";
import Habits from "./components/habits";
import React, { PureComponent } from "react";
import Navbar from "./components/navbar";

class App extends PureComponent {
  state = {
    habits: [
      { id: 1, name: "Reading", count: 0 },
      { id: 2, name: "Running", count: 0 },
      { id: 3, name: "Coding", count: 0 },
    ],
  };

  handleInclement = (habit) => {
    const habits = this.state.habits.map((item) => {
      if (item.id === habit.id) {
        return { ...habit, count: habit.count + 1 };
      } else {
        return item;
      }
    });
    this.setState({ habits });
  };

  handleDecreament = (habit) => {
    const habits = this.state.habits.map((item) => {
      if (item.id === habit.id) {
        const count = habit.count - 1;
        return { ...habit, count: count < 0 ? 0 : count - 1 };
      } else {
        return item;
      }
    });
    // const habits = [...this.state.habits];
    // const index = habits.indexOf(habit);
    // const newCount = habits[index].count - 1;
    // habits[index].count = newCount < 0 ? 0 : newCount;
    this.setState({ habits });
  };

  handleDelete = (habit) => {
    const habits = this.state.habits.filter((s) => s.id !== habit.id);
    this.setState({ habits });
  };

  resetAll = () => {
    const habits = this.state.habits.map((habit) => {
      console.log(habit.count);
      if (habit.count !== 0) {
        return { ...habit, count: 0 };
      } else {
        return habit;
      }
    });
    // const habits = [...this.state.habits];
    // habits.map((s) => (s.count = 0));
    this.setState({ habits });
  };

  handleReset = () => {
    const habits = this.state.habits.map((habit) => {
      habit.count = 0;
      return habit;
    });
    this.setState({ habits });
  };

  addNewHabbit = (newHabitNm) => {
    const habits = [...this.state.habits];
    habits.append({
      id: this.state.habits.length + 1,
      name: newHabitNm,
      count: 0,
    });
  };

  handleAdd = (name) => {
    const habits = [...this.state.habits];
    const newhabit = { id: habits.length + 1, name, count: 0 };
    habits.push(newhabit);

    this.setState({ habits });
  };

  render() {
    return (
      <section>
        <Navbar
          totalCount={this.state.habits.filter((s) => s.count > 0).length}
          onNewHabit={this.addNewHabbit}
        />

        <Habits
          habits={this.state.habits}
          onIncrement={this.handleInclement}
          onDecrement={this.handleDecreament}
          onDelete={this.handleDelete}
          onAdd={this.handleAdd}
          onReset={this.resetAll}
        />
        <button onClick={this.resetAll}>RESET ALL</button>
      </section>
    );
  }
}

export default App;
